class VocabularySingle extends React.Component {
  constructor(props) {
    super(props);
  }
  
  render() {
    return(
      <div className="voc">
        <span className="german">{this.props.german}</span>
        <span className="english">{this.props.english}</span>
        <span className="chinese">{this.props.chinese}</span>
      </div>
    );
  }
}


class PracticeText extends React.Component {
  render() {
    return (
      <div className="practice">
        <h3> bungstext  Reading Practice  </h3>
  
        <p className="german">
          <span className="name">Hanna:</span> Hallo, ich bin Hanna. Wie heit du?<br/><br/>
    
          <span className="name">Marie:</span> Hallo, Hanna. Ich heie Marie. Freut mich dich kennenzulernen!<br/><br/>
    
          <span className="name">Hanna:</span> Freut mich auch!
        </p>
        <p className="english">
          <span className="name">Hanna:</span> Hello, I am Hanna. What is your name?<br/><br/>
    
          <span className="name">Marie:</span> Hello, Hanna. My name is Marie. Nice to meet you! Literally: I am happy to get to know you!)<br/><br/>
          
          <span className="name">Hanna:</span> Nice to meet you too! (Lit.: I am happy too!)
        </p>
      
        <PracticeTextBox language="chinese" text={[
          <span className="name">Hanna:</span>, 
          "Hanna  Lit.: ",
          <br/>, <br/>, 
          <span className="name">Marie:</span>,
          "Hanna Marie ",
          <br/>, <br/>,
          <span className="name">Hanna:</span> ,
          ""
          ]} />
      
      </div>
    );
  }
}

class PracticeTextBox extends React.Component {
  constructor(props) {
    super(props);
  }
  
  render(){
    return (
      <p className={this.props.language}>
        {this.props.text}
      </p>
    );
  }
  
}

class MainComponent extends React.Component {
  constructor(props) {
    super(props);
    this.handlePageClick = this.handlePageClick.bind(this);
    this.state = {currentPage: 0};
  }

  handlePageClick(pageId) {
    this.setState({currentPage: pageId});
  }
  
  /*
  handlePageButtonClick(clickedPageId) { //passed by/to Menu component (in menu's "onClick" prop)
    this.setState({currentpage: clickedPageId});
  }
  */
  
  render() {
    let pageContent = null;
    if(this.state.currentPage == 0){
      pageContent = <StartPage />;
    }
    
    if(this.state.currentPage == 1){
      pageContent = <LessonPage lessonId="1" />
    }
    
    return (
      <div>
        <NavBar onClick={this.handlePageClick}/>
        {pageContent}
        <AllLessons />
      </div>
    );
  }
}

class StartPage extends React.Component {
  render() {
    return(
      <div>
        <h2>Introduction to this German class</h2>
        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
        </p>
      </div>
    );
  }
}

class AllLessons extends React.Component {
  render() {
    return(
      <div id="all">
        <ul>
          <li><a href="./lesson01.html">Lektion 1  Lesson 1  </a></li>
          <li><a href="./lesson_numbers.html">Zahlen  Numbers  </a></li>
          <li><a href="./lesson_abc.html">Das Alphabet  The alphabet  </a></li>
        </ul>
      </div>
    );
  }
}

class NavBar extends React.Component {
  render() {
    return(
      <div className="navbar">
        <NavElem onClick={this.props.onClick} contentId="0" navTitle="Home" />
        <NavElem navUrl="#all" navTitle="Alle Lektionen  All lessons  " /> |
        <NavElem onClick={this.props.onClick} contentId="1" navTitle="Nchste Lektion   Next lesson  " />
      </div>
    );
  }
}

class NavElem extends React.Component {
  constructor(props) {
    super(props);
    this.navItemClicked = this.navItemClicked.bind(this)
  }
  
  navItemClicked() {
    this.props.onClick(this.props.contentId);
  }
  
  render() {
    if(this.props.navUrl != null){
      return(
        <a href={this.props.navUrl}>{this.props.navTitle}</a>
      );
    }
    else {
      return(
        <div onClick={this.navItemClicked.bind(this, this.props.contentId)} className="navItem">{this.props.navTitle}</div> 
      );
    }
  }
}

ReactDOM.render(
  <MainComponent />,
  document.getElementById('react-elem')
);


